// session
function getAllOpenPage() {
  const firstPage = {
    icon: 'el-icon-tickets',
    label: '',
    path: '/home'
  }
  const state = sessionStorage.getItem('vuex');
  if(state && JSON.parse(state).allOpenPage) {
    return JSON.parse(state).allOpenPage;
  }
  return [firstPage];
}

export { getAllOpenPage }